@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(//fonts.googleapis.com/css?family=Work+Sans:300,400,600);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,::before,::after {
  box-sizing: border-box;
}

.task-title {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  margin-bottom: 30px;
  
  font-size: 30px;
  text-decoration: underline;
}

.workout-box {
  width: 600px;
  padding: 30px;
  margin-bottom: 20px;

  border: 2px solid;
}

.workout-form {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.workout-section {
  display: flex;
  flex-direction: column;
}

button {
  align-self: flex-end;
  width: 35px;
  height: 35px;

  cursor: pointer;
}

label {
  margin-bottom: 10px;

  font-style: italic;
}

.workout-input {
  height: 35px;
  padding: 5px 10px;

  border: 1px solid gray;
  border-radius: 10px;
  outline: none;
}

.workout-list {
  width: 100%;
}

.workout-list_titles {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;

  font-weight: 700;
}

.workout-item {
  display: flex;
  justify-content: space-between;
}

.workout-item_actions {
  display: flex;
  width: 65px;
  justify-content: space-around;
  align-items: center;
}

.material-icons {
  font-family: 'Material Icons';

  cursor: pointer;
}

